@import '../mixin.module.scss';

.PDFViewer {
  div {
    div {
      margin-top: 10px;
      canvas {
        margin: 0px auto;
      }
      div {
        border: 1px solid blue;
      }
    }
  }
}

.react-pdf__Page__canvas {
  border: 2px solid black;
}

